<template>
  <div class="hello">
    <div class="title">酒店后台管理系统</div>
    <div class="box">     
      <div><img src="../assets/logo_02.png" alt=""></div><br>       
      <div>用户名：<input type="text" v-model:value="uname"></div><br>
      <div>密&nbsp;&nbsp;&nbsp;&nbsp;码：<input type="password" v-model:value="password">
      </div><br>
      <button v-on:click="login" class="btn">登&nbsp;&nbsp;&nbsp;陆</button>   
    </div>
  </div>
</template>
<script>  
import axios from 'axios' 
export default {
  name: 'Login',
  data () {
    return {
      blogs:[],
      uname:'',
      loginState:'',
      password:'',
    }
  },
  created:function(){

  },
  methods:{
    login:function(){
      var _this = this;
      console.log(_this.uname);
      $.ajax({
            type:'post',
            url:'http://172.16.6.90:8388/login_hotel',
            headers:{
                "Conten-Type":"http://172.16.6.90:8388/login_hotel"
            },
            data: "uname="+_this.uname+'&password='+_this.password,
            success:function (backDate) {
              _this.loginState = backDate.loginState;
                
                if(_this.loginState=='no'){
                  // alert('登陆失败');
                  _this.$message({
                    type: 'error',
                    message: '登陆失败!'
                  });
                }else{
                  // 这里的this已经改变，需要用原来的 _this 代替，或箭头函数
                  _this.$store.commit('setUserName',_this.uname);
                  _this.$router.push({path:'/home'});
                }
                console.log(_this.loginState);
            }
        });
    },
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .hello{
    width: 100%;
    height: 900px;
    position: relative;
    background:url("../assets/loginbg.jpg");
    background-size: 100% 100%;
    padding-top: 1px;
 }
 .bg{
    position: absolute;
    left:0px;
    top:0px;
    width: 100%;
    height: 400%;
 }
 .box{
    width: 300px;
    height: 200px;
    margin:50px auto;
    background-color: #fff;
    border-radius: 5px;
    text-align: center;
 }
 .title{
    font-size:50px;
    color:#fff;
    text-shadow: 1px 2px;
    margin-top: 300px;
 }
 .btn{
   font-size: 18px;
   width: 150px;
   height: 25px;
   border:0px solid red;
   border-radius: 3px;
   line-height: 25px;
   background-color: rgb(185,39,45);
   color:#fff;
 }
 .btn:hover{
    cursor: pointer;
    background-color: rgb(155,39,45);
 }
</style>



