<template>
	<div class="hello">
		<el-container style="height: 890px; border: 1px solid #eee">
			<el-header style="font-size: 24px">
				<div style="position:relative" >
					<span  style="position:absolute;left:5px;color:#fff;font-size:40px;text-shadow:2px 2px;">xxx酒店后台管理系统</span>
					<span style="width:100px;color:#fff;font-size:14px;position:absolute;right:5px;">管理员：{{userName}}
					</span>
				</div>        
			</el-header>
			<el-main>
				<el-container style="height: 780px; border: 1px solid #eee">
					<el-aside width="200px">
						<el-menu :default-openeds="['4']" unique-opened='true'  background-color="#66A4E3" active-text-color="#F9FAFC" default-active="4-2">
							<el-submenu index="1">
								<template slot="title"><i class="el-icon-message"></i>产品管理</template>
								<el-menu-item v-on:click="productManagement" index="1-1">产品管理</el-menu-item>
							</el-submenu>
							<el-submenu index="2">
								<template slot="title"><i class="el-icon-menu"></i>新闻管理</template>	
								<el-menu-item v-on:click="newsManagement"  index="2-1">新闻管理</el-menu-item>					
							</el-submenu>
							<el-submenu index="3">
								<template slot="title"><i class="el-icon-edit-outline"></i>设施管理</template>	
								<el-menu-item  v-on:click="facilitiesManagement" index="3-1">设施管理</el-menu-item>					
							</el-submenu>

							<el-submenu index="4">
								<template slot="title"><i class="el-icon-setting"></i>其他管理</template>
								<el-menu-item v-on:click="companyManagement"  index="4-1">公司简介</el-menu-item>
								<el-menu-item v-on:click="onlineManagement"  index="4-2">用户留言</el-menu-item>
								<el-menu-item v-on:click="administratorManagement"  index="4-3">账号管理</el-menu-item>
							</el-submenu>
						</el-menu>
					</el-aside>

					<el-container>
						<el-header style=" font-size: 12px">
							用户留言
						</el-header>

						<el-main>
							<div class="company_culture">
								
							</div>
							　
						</el-main>
					</el-container>
				</el-container>
			</el-main>
		</el-container>

	</div>
</template>
<script>   
	export default {
		name: 'Home',
		data() {
			return {
				userName:''
			}
		},
		created:function(){
			if(''==this.$store.state.currentUserName){
				this.$router.push({path:'/'});//未登录跳转回登陆界面
			}else{
				this.userName = this.$store.state.currentUserName;
			}
		},
		methods:{			
			productManagement:function(){
				var _this = this;
				_this.$router.push({path:'/ProductManagement'});
				console.log("跳转到产品管理页面");
			},
			newsManagement:function(){
				var _this = this;
				_this.$router.push({path:'/NewsManagement'});
				console.log("跳转到新闻管理页面");
			},
			facilitiesManagement:function(){
				var _this = this;
				_this.$router.push({path:'/FacilitiesManagement'});
				console.log("跳转到设施管理页面");
			},
			companyManagement:function(){
				var _this = this;
				_this.$router.push({path:'/CompanyManagement'});
				console.log("跳转到公司简介管理页面");
			},
			onlineManagement:function(){
				var _this = this;
				_this.$router.push({path:'/OnlineManagement'});
				console.log("跳转到留言管理页面");
			},
			administratorManagement:function(){
				var _this = this;
				_this.$router.push({path:'/AdministratorManagement'});
				console.log("跳转到管理员管理页面");
			}
		}
	}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.el-header {
		background-color: #7AB9FB;
		color: #333;
		line-height: 60px;
		text-align: center;
	}

	.el-aside {
		color: #333;
		background-color: #7AB9FB;
	}
	.company_culture{
		font-size: 30px;
		color:red;
	}
	.el-menu-item{
		color:red;
	}
</style>



