<script> 
import axios from 'axios'  
  	/*
  	houseID: '2304',
    houseType: '豪华套房',
    price: '888',
    area: '80平米',
    bed:'1',
    du_li_wei_yu: '√',
    wifi:'√',
    yang_tai:'√',
    breakfast:'√',
    houseImg:'254571224125ss.jpg'
    <img src="http:\/\/172.16.6.90:8388/imgs/1517192696398.jpg" alt="图片加载失败">
    */
	export default {
		name: 'Home',
		data() {
			return {
        userName:'',
				currentIndex_img_url:'',
				currentIndex_img_url_before:'',
				currentIndex:0,//当前行index
				dialogVisible:false,
				currentPage4: 4,
				totals:0,
				tableData:[
					{
						houseID: '2304',
				    houseType: '豪华套房',
				    price: '888',
				    area: '80平米',
				    bed:'1',
				    du_li_wei_yu: '√',
				    wifi:'√',
				    yang_tai:'√',
				    breakfast:'√',
				    houseImg:'254571224125ss.jpg',
				    _id:''
					}
				],
				houseMsgs:{//添加数据（模板）
						houseID: '',
				    houseType: '',
				    price: '',
				    area: '',
				    bed:'',
				    du_li_wei_yu: '',
				    wifi:'',
				    yang_tai:'',
				    breakfast:'',
				    houseImg:'',
				    _id:''
				},
				dialogFormVisible: false,
				dialogFormVisible_add: false,
				dialogFormVisible_more:false,
        formLabelWidth: '100px'
			}
		},
		created:function(){
			var _this = this;
			this.currentIndex_img_url_before = "http://172.16.6.90:8388/imgs/";
      if(''==this.$store.state.currentUserName){
        this.$router.push({path:'/'});//未登录跳转回登陆界面
      }else{
        this.userName = this.$store.state.currentUserName;
      }
			/*--------------页面加载后直接查询房间信息并且渲染出来----------*/
			$.ajax({
            type:'get',
            url:'http://172.16.6.90:8388/houseMsgAll',
            headers:{
                "Conten-Type":"http://172.16.6.90:8388/houseMsgAll"
            },
            success:function (backDate) {
              _this.tableData = backDate.houseMsg;
              _this.totals = _this.tableData.length;
            }
        });			
		},
		methods:{
				changeImage(e) {
					var file = e.target.files[0]
					var reader = new FileReader()
					var that = this
					reader.readAsDataURL(file)
					reader.onload = function(e) {
						that.avatar = this.result
					}
				},
				handleInsert:function(){
					var _this = this;

					if (this.$refs.avatarInput.files.length !== 0) {
						var formdata = new FormData()//声明表单数据对象
						formdata.append('img', this.$refs.avatarInput.files[0]);
						formdata.append('houseID',_this.houseMsgs.houseID);
						formdata.append('houseType',_this.houseMsgs.houseType);
						formdata.append('price',_this.houseMsgs.price);
						formdata.append('area',_this.houseMsgs.area);
						formdata.append('bed',_this.houseMsgs.bed);
						formdata.append('du_li_wei_yu',_this.houseMsgs.du_li_wei_yu);
						formdata.append('wifi',_this.houseMsgs.wifi);
						formdata.append('yang_tai',_this.houseMsgs.yang_tai);
						formdata.append('breakfast',_this.houseMsgs.breakfast);

						axios.post('http://172.16.6.90:8388/add_houseMsg', formdata, {
	          headers: {
	          	"Content-Type": "multipart/form-data"
	          }
	        }).then(function(backDate){
	        	console.log(backDate);
	        	if(backDate.data.addState===1){
	        		_this.houseMsgs.houseImg = backDate.data.houseImg;
	        		_this.houseMsgs._id = backDate.data._id;
	        		console.log(_this.houseMsgs);
	        		_this.tableData.push(_this.houseMsgs);
	        		_this.totals = _this.tableData.length;
	        		_this.dialogFormVisible_add = false;
							_this.$message({
                		type: 'success',
                		message: '上传成功!'
                	});
							console.log("上传成功");
	        	}else{
	        		console.log("上传失败");
	        	}
	        	
	        	
	        }) 
	      }
	    } ,
			handleCommand_HouseWifi(command) {
        this.houseMsgs.wifi = command;
      },
      handleCommand_HouseDuLiWeiYu(command) {
        this.houseMsgs.du_li_wei_yu = command;
      },
      handleCommand_HouseYangTai(command) {
        this.houseMsgs.yang_tai = command;
      },
      handleCommand_HouseBraekfast(command) {
        this.houseMsgs.breakfast = command;
      },
      handleCommand_HouseBed(command) {
        this.houseMsgs.bed = command;
      },
      handleCommand_HouseType(command) {
        this.houseMsgs.houseType = command;
      },
			handleInsert1(){//插入数据
				var _this = this;

			},
			update_houseMsg(){//更新数据
				var _this = this;

				this.$confirm('该数据将与客户端页面同步，请核实信息无误后再点击确认', '提示', {
          confirmButtonText: '确定更新',
          cancelButtonText: '再核实一便',
          type: 'warning'
        }).then(() => {
        	console.log('确定更新');
        	
        	$.ajax({
            type:'post',
            url:'http://172.16.6.90:8388/update_houseMsg',
            headers:{
                "Conten-Type":"http://172.16.6.90:8388/update_houseMsg"
            },
            data: "_id="+_this.tableData[_this.currentIndex]._id+
            			"&houseID="+_this.tableData[_this.currentIndex].houseID+
            			"&houseType="+_this.tableData[_this.currentIndex].houseType+
            			"&price="+_this.tableData[_this.currentIndex].price+
            			"&area="+_this.tableData[_this.currentIndex].area+
            			"&bed="+_this.tableData[_this.currentIndex].bed+
            			"&du_li_wei_yu="+_this.tableData[_this.currentIndex].du_li_wei_yu+
            			"&wifi="+_this.tableData[_this.currentIndex].wifi+
            			"&yang_tai="+_this.tableData[_this.currentIndex].yang_tai+
            			"&breakfast="+_this.tableData[_this.currentIndex].breakfast+
            			"&houseImg="+_this.tableData[_this.currentIndex].houseImg,
            success:function (backDate) {   
            		console.log(backDate.updateState);            
                if(backDate.updateState===1){
                	_this.$message({
                		type: 'success',
                		message: '更新数据成功!'
                	});  
                	_this.dialogFormVisible = false;	//关闭编辑对话框
                }else{
                  _this.$message({
				            type: 'error',
				            message: '更新失败!'
				          });
				         _this.dialogFormVisible = false; //关闭编辑对话框
                }
            }
        	});
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });          
        });
			},
			handleSelectionChange(rows) {
        this.multipleSelection = rows;
        console.log(rows.length);
        if (rows) {
          rows.forEach(row => {
            console.log(row.address);
          });        
      }},
			handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
			tableRowClassName({row, rowIndex}) {
        if (rowIndex%2 === 0) {
          return 'success-row';
        }
        return '';
      },
      handleQuery(index, row,_id){
      	var _this = this;
      	_this.dialogFormVisible_more = true;
      	_this.currentIndex = index;
      	_this.currentIndex_img_url = _this.currentIndex_img_url_before+_this.tableData[_this.currentIndex].houseImg;
      	console.log(_this.currentIndex_img_url);

      },
			handleEdit(index, row,_id) {
				var _this = this;
				console.log("编辑："+index);
				_this.dialogFormVisible=true;
				this.currentIndex = index;
			},
			handleDelete(index, row,_id) {//删除数据
				var _this = this;
				console.log("删除:"+index);
				
				this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {

        	$.ajax({
            type:'post',
            url:'http://172.16.6.90:8388/del_houseMsg',
            headers:{
                "Conten-Type":"http://172.16.6.90:8388/del_houseMsg"
            },
            data: "_id="+_id,
            success:function (backDate) {   
            		console.log(backDate.delState);            
                if(backDate.delState===1){
                	_this.$message({
                		type: 'success',
                		message: '删除成功!'
                	});

                	//只要删除数组就可以删除表格当前行
                	_this.tableData.splice(index, 1);
                	_this.totals = _this.tableData.length;
                	
                }else{
                  _this.$message({
				            type: 'error',
				            message: '删除失败!'
				          });
                }
            }
        	});
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });				
			},			
			productManagement:function(){
				var _this = this;
				_this.$router.push({path:'/ProductManagement'});
				console.log("跳转到产品管理页面");
			},
			newsManagement:function(){
				var _this = this;
				_this.$router.push({path:'/NewsManagement'});
				console.log("跳转到新闻管理页面");
			},
			facilitiesManagement:function(){
				var _this = this;
				_this.$router.push({path:'/FacilitiesManagement'});
				console.log("跳转到设施管理页面");
			},
			companyManagement:function(){
				var _this = this;
				_this.$router.push({path:'/CompanyManagement'});
				console.log("跳转到公司简介管理页面");
			},
			onlineManagement:function(){
				var _this = this;
				_this.$router.push({path:'/OnlineManagement'});
				console.log("跳转到留言管理页面");
			},
			administratorManagement:function(){
				var _this = this;
				_this.$router.push({path:'/AdministratorManagement'});
				console.log("跳转到管理员管理页面");
			}
		}
	}

</script>

