import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'
import Login from '@/components/Login'
import Home from '@/components/Home'
import ProductManagement from '@/components/ProductManagement'
import NewsManagement from '@/components/NewsManagement'
import FacilitiesManagement from '@/components/FacilitiesManagement'
import CompanyManagement from '@/components/CompanyManagement'
import OnlineManagement from '@/components/OnlineManagement'
import AdministratorManagement from '@/components/AdministratorManagement'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/ProductManagement',
      name: 'ProductManagement',
      component: ProductManagement
    },
    {
      path: '/NewsManagement',
      name: 'NewsManagement',
      component: NewsManagement
    },
    {
      path: '/FacilitiesManagement',
      name: 'FacilitiesManagement',
      component: FacilitiesManagement
    },
    {
      path: '/CompanyManagement',
      name: 'CompanyManagement',
      component: CompanyManagement
    },
    {
      path: '/OnlineManagement',
      name: 'OnlineManagement',
      component: OnlineManagement
    },
    {
      path: '/AdministratorManagement',
      name: 'AdministratorManagement',
      component: AdministratorManagement
    }
  ]
})



